<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-targeted-promo-banner",
    {
      "c-targeted-promo-banner--fixed": fixed
    }
  ]`
)
  .c-targeted-promo-banner__container
    .c-targeted-promo-banner__flag

    div(
      v-html="formattedContent"
      class="c-targeted-promo-banner__content"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
export default {
  name: 'TargetedPromoBanner',

  props: {
    code: {
      type: String,
      required: true
    },
    content: {
      type: String,
      required: true
    },
    fixed: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      // --> STATE <--

      country: null,
      city: null
    }
  },

  computed: {
    formattedContent() {
      return this.content
        .replace(/{{code}}/g, this.code)
        .replace(/{{country}}/g, this.country)
        .replace(/{{city}}/g, this.city)
    }
  },

  created() {
    // Call http://ip-api.com/json to get the country
  }
}
</script>

